import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ViewBillingComponent } from '../view-billing/view-billing.component'
import { BillingService } from '../../../../core/auth/_services/billing.service';
import { Billing } from '../../../../core/auth/_models/billing.model';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';

@Component({
  selector: 'kt-billing',
  templateUrl: './billing.component.html',
})
export class BillingComponent implements OnInit {

  filterStatus: string = '';
  displayedColumns = ['ClubName', 'PackageName', 'StartDate', 'EndDate', 'Discount', 'status', 'actions'];
  clubName;
  billing: Billing;
  dataSource;
  club_id;
  NoDataMessage;
  billingForm:  FormGroup;
	hasFormErrors: boolean = false;
  viewLoading: boolean = false;
  status;
  statusClass;


  constructor(private billingService: BillingService, public dialog: MatDialog, private changeDetectorRefs: ChangeDetectorRef,private fb: FormBuilder,) { }

  ngOnInit() {
    let now = new Date();
    let monthAgo = new Date();
     monthAgo.setMonth(now.getMonth() - 1);
    this.billingForm = this.fb.group({
      start: [monthAgo, [Validators.required]],
      end: [now, [Validators.required]],
      })

  }
  findBilling(start_date, end_date) {
    const controls = this.billingForm.controls;
    if (this.billingForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);
			this.hasFormErrors = true;
			return;
		}
    var d = new Date(start_date);
    start_date = d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();
    var d2 = new Date(end_date);
    end_date = d2.getFullYear() + "-" + (d2.getMonth() + 1) + "-" + d2.getDate();
    this.club_id = JSON.parse(localStorage.getItem('user'))['pub_key'];
    this.billingService.getBilling(start_date, end_date, this.club_id).subscribe(res => {
      let result = res['result'];
      let data = res['data'][0];
      if (result == true && typeof data != "undefined") {
        this.NoDataMessage=" ";
        this.clubName=JSON.parse(localStorage.getItem('user'))['name'];
        this.billing = res['data'][0];
        this.dataSource = [this.billing];
        console.log(this.dataSource);
        console.log(result);
        console.log('data')
        this.changeDetectorRefs.detectChanges();
      }
      else if(result == true && typeof data == "undefined") {
        this.clubName= '';
        this.dataSource= '';
        this.NoDataMessage="There is no invoice for this date";
        this.changeDetectorRefs.detectChanges();
        console.log('no data',this.NoDataMessage)
       }
       else if(result==false) {
        this.clubName= '';
        this.dataSource= '';
        this.NoDataMessage="Insert valid date to show the invoice";
        this.changeDetectorRefs.detectChanges();
        console.log('no data',this.NoDataMessage)
       }
      
      console.log(res)
    })
  }
  viewBilling(billing) {
    const dialogRef = this.dialog.open(ViewBillingComponent, {
      width: '80%',
      height: '80%',
      data: { 'billing': billing, 'clubName': this.clubName }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

  


	}

	
	



