import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { BillingService } from '../../../../core/auth/_services/billing.service';
import { billingDetails } from '../../../../core/auth/_models/billing-details.model';
import { Billing } from '../../../../core/auth/_models/billing.model';
import {UserService} from '../../../../core/auth/_services/-user.service'

@Component({
  selector: 'kt-view-billing',
  templateUrl: './view-billing.component.html',
  styleUrls: ['./view-billing.component.scss']
})
export class ViewBillingComponent implements OnInit {
  revenue;
  billing: Billing;
  clubName;
  TotalRevneu = 0;
  billingDetails: [billingDetails];
  payment;
  paymentStatus;
  club_id=JSON.parse(localStorage.getItem('user'))['pub_key'];
  branch;
  branchName;
  totalTransaction=0;
  @ViewChild('billingFile') billingFile: ElementRef;
  constructor(
    public dialogRef: MatDialogRef<ViewBillingComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private billingService: BillingService,
    private userService:UserService
  ) { }

  ngOnInit() {
    this.billing = this.data.billing;
    this.clubName = this.data.clubName;
    let start_date=this.billing.start_date
    let end_date= this.billing.end_date
   var d = new Date(start_date);
   start_date = d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();
   var d2 = new Date(end_date);
   end_date = d2.getFullYear() + "-" + (d2.getMonth() + 1) + "-" + d2.getDate();
   this.billingService.getBillingDetails(start_date, end_date, this.club_id)
      .subscribe(res => {
        this.billingDetails = res;
       
        for (let i = 0; i < this.billingDetails.length; i++) {
          if(this.billingDetails[i].calc==0){
              this.billingDetails[i].tRevneu=0;
              this.billingDetails[i].tTrans=0;
          }
         
          this.TotalRevneu = this.TotalRevneu + this.billingDetails[i].tRevneu;
          this.totalTransaction=this.totalTransaction + this.billingDetails[i].tTrans
          // this.userService.getUserbyId(this.billingDetails[i].branch).subscribe(res=>{
          //      this.branch=res[i];
          //      this.branchName=this.branch.username
          // })
        }
        this.payment = Math.round((this.billing.sub_total - (this.billing.sub_total * this.billing.discount / 100)))
      });
  }
  print(): void {
    let printContents, popupWin;
    printContents = document.getElementById('billingFile').innerHTML;
    popupWin = window.open('', '_blank', 'top=0,left=0,height=100%,width=auto');
    popupWin.document.open();
    popupWin.document.write(`
      <html>
        <head>
          <title>Gymin Invoice</title>
          <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css" integrity="sha384-ggOyR0iXCbMQv3Xipma34MD+dH/1fQ784/j6cY/iJTQUOhcWr7x9JvoRxT2MZw1T" crossorigin="anonymous">
          <style>
          body {
            background: grey;
            margin-top: 30px;
            margin-bottom: 30px;
        }
          </style>
        </head>
    <body onload="window.print();window.close()">${printContents}</body>
      </html>`
    );
    popupWin.document.close();
}


}
